// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Vuex from 'vuex'
import App from './App'
import VueRouter from 'vue-router';
import VueResource from 'vue-resource';
import { routes } from './routes';
import 'bootstrap/dist/css/bootstrap.css';

Vue.config.productionTip = false

Vue.use(VueResource);
Vue.use(VueRouter);
Vue.use(Vuex);

/*------- Vuex -------*/
const store = new Vuex.Store({
  state: {
    cart: {
      items: []
    }
  },
  getters: {
    cartTotal: (state) => {
      let total = 0;

      state.cart.items.forEach(function(item) {
        total += item.product.price * item.quantity
      });
      return total;
    },

    taxAmount: ( state, getters ) => {
      percentage =>  {
        return ((getters.cartTotal * percentage) / 100 );
      };
    // taxAmount: ( state, getters ) => {
    //   return function(percentage) {
    //     return ((getters.cartTotal * percentage) / 100 );
    //   };

    // return((getters.cartTotal * 10) / 100 );
    }
  }
});
/*------- End -------*/

Vue.filter('currency', function(value) {
  let formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
    minimumFractionDigits: 0
  });

  return formatter.format(value);
});

export const eventBus = new Vue();
export const authService = { isLoggedIn: false };


const router = new VueRouter({
  routes: routes,
  mode: 'history',
  scrollBehavior(to, from, savedPosition) {
    if (to.hash) {
      return {
        selector: to.hash
      };
    }

    if (savedPosition) {
      return savedPosition;
    }

    return { x: 0, y: 0 };
  }
});

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.isAuthRequired)) {
    if (!authService.isLoggedIn) {
      alert("You must be logged in!");
      return next(false);
    }
  }

  next();
});

Vue.http.options.root = 'http://localhost:3000';
Vue.http.headers.common['X-Requested-With'] = 'XMLHttpRequest';

Vue.http.interceptors.push((request, next) => {
    request.headers.set('X-CSRF-TOKEN', 'VERY_SECURE_TOKEN_HERE');
    next((response) => {
        console.log(response);
    });
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  render: h => h(App),
  router: router,
  store: store
});



